@import './src/App.scss';
@mixin heading {
  color: $body;
  text-align: center;
  background: $gold;
  padding: 50px 20px;
}

.heading {
   @include heading;
  } 
  
#contact-us {
      margin-bottom: 50px;
     .contact-us {
       padding: 20px;
       background: $blue-hue; 
       margin: 100px 20px;
     .contact {
       @include flex;
       justify-content: flex-start;
       flex-direction: row;
       border: 2px $gold solid;
       border-radius: 10px;
       padding: 10px;
       margin: 20px 0;
       h1 {
         color: $gold;
       }
     .icon {
        color: gold;
        font-size: 80px;
        border: 2px $gold solid;
        border-radius: 10px;
        padding: 20px;
        margin: 20px;
      }
     .content {
       h1 {
         text-align: left;
         margin-bottom: 10px;
       }
       p{
         color: white;
       }
     }
      }
     }
}

@media (min-width: 688px){
  #contact-us {
  .container {
    @include flex;
    flex-direction: row;
    align-items: flex-end;
   .icon {
     font-size: 20px;
     padding: 8px;
   }
  }
}

} 