$dark-blue: #02081D;
$light-blue: white;
$gold:  #ffa300;
$blue-hue: rgb(2,26,29);
$transition: all 0.3s ease-in-out;
$shadow: 3px 3px 8px 2px rgba(0,0,0,0.7);
$light-gray: #afafaf;
$body: #2c3e50;

@mixin headings {
  font-size: 25px;
  color: $gold;
  text-align: center;
  margin-bottom: 20px;
  text-transform: capitalize;
}

@mixin flex {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
};

@mixin icon {
   border: 2px $gold solid;
    border-radius: 100%;
    padding: 15px;
    color: $gold;
    font-size: 4rem;
    margin-bottom: 20px;
    transition: all 0.9s ease-in-out;
    &:hover {
      transform: rotate(360deg) scale(1.1);
    }
}

@mixin section-img {
  border: 4px $gold solid;
  max-width: 100%;
  box-shadow: $shadow;
  transition: $transition;
  &:hover {
     transform: scale(1.01);
   } 
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: $body;
  overflow-x: hidden;
}

header {
 background: $dark-blue;
 @include flex;
 padding: 15px;
.logo {
  font-size: 3rem;
  color: $gold;
  font-weight: 500;
  span {
    color: red;
    font-size: 3.25rem;
    font-style: italic;
    font-weight: 900;
  }
}
.numbers, .open-time {
  display: none;
}
}

.social {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 0.5rem;
 .social-icon {
   color: white;
   font-size: 1.2rem;
 }
}


#menu {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  grid-gap: 50%;
  align-items: center;
  background: $blue-hue;
  padding: 10px 0;
  p {
    color: white;
    font-size: 1rem;
    text-transform: uppercase;
    transition: $transition;
    &:hover, &:active {
      color: $gold;
    }
  }
  
.menu-icon {
  font-size: 20px;
  color: white;
  transition: $transition;
    &:hover, &:active {
      color: $gold;
    }
  }  
  nav {
    margin-top: -5rem;
    margin-left: 20px;
    .menu {
      padding-bottom: 8rem;
      li{
        list-style: none;
        margin-top: 10px;
        a {
          text-align: left;
          color: white;
          font-size: 1rem;
          text-decoration: none;
          transition: $transition;
          &:hover {
            color: $gold;
          }
        }
      }
    }
  } 
} 

button {
  background: $blue-hue;
  padding: 8px 15px;
  border: 2px $gold solid;
  transition: $transition;
  margin-top: 20px;
  &:hover {
    background: $gold;
  }
  &:hover a{
    color: $blue-hue;
  }
  a {
    font-size: 15px;
    color: $gold;
    text-decoration: none;
    text-transform: uppercase;
    transition: $transition;
    &:hover {
      color: $blue-hue;
    }
  }
}

.qualities {
  @include flex;
   background: $blue-hue;
   margin-top: -5px;
   position: relative;
   z-index: 10;
 .quality {
   @include flex;
   border-bottom: 0.05px gray solid;
   width: 100%;
   padding: 35px 20px;
  .quality-icon {
   @include icon;
  }
   h1 {
     @include headings;
   }
   p {
     color: rgb(105,105,105);
     text-align: center;
   }
 }
}

#team {
  overflow: hidden;
  @include flex;
  padding: 20px;
  img, h1, p, button {
    margin-top: 30px;
  }
  img {
    @include section-img;
  }
  h1 {
    @include headings;
    text-transform: capitalize;
    margin-bottom: -10px;
  }
  
  p {
    color: $light-gray;
  }
}

#specialties {
  @include flex;
  padding: 0 20px;
  margin-top: 50px;
  h1 {
    @include headings;
  }
  .specialty {
    @include flex;
    margin-top: 50px;
    background: $blue-hue;
   .specialty-icon {
     background: $gold;
     width: 100%;
     @include flex;
     .icon {
       @include icon;
       background: $light-blue;
       margin: 20px;
     }
   } 
   .specialty-content {
     @include flex;
     padding: 30px 20px;
     h1 {
       @include headings;
     }
     p{
       text-align: center;
       color: $light-gray;
     }
   }
  }
} 

.why-us {
  overflow: hidden;
   .img {
     padding: 20px;
      img {
        @include section-img;
    }
   }
    margin-top: 100px;
    h1 {
      @include headings;
    }
  .reason {
     display: grid;
     grid-template-columns: 1fr 2fr;
     grid-gap: 20px;
     padding: 20px;
     h1 {
       text-align: left;
     }
     p {
       color: white;
     }
     #icon {
     background: $blue-hue;
     border-radius: 10px;
     @include flex;
     .icon {
      @include icon;
      margin-bottom: 0;
    }
   }
  } 
}

.attorneys {
  padding: 20px;
  @include flex;
  margin-top: 100px;
  h1 {
    @include headings;
  } 
   .attorney {
     @include flex;
     margin: 20px;
     background: $blue-hue;
     overflow: hidden;
     border-radius: 10px;
     img {
       max-width: 100%;
     }
   .content {
       position: relative;
      .socials {
          @include flex;
          background: $gold;
          width: 100%;
          flex-direction: row;
          position: absolute;
          top: -20%;
          transition: $transition;
          &:hover {
            width: 200%;
          }
         .icon {
           margin: 8px;
           font-size: 25px;
           transition: $transition;
           &:hover {
             color: white;
             transform: scale(1.1);
           }
        }
      } 
     @include flex;
     padding: 40px;
     p {
       margin-top: -16px;
       color: $light-gray;
     }
   }
   }
  }

.FAQ {
    margin-top: 100px;
  h1 {
    @include headings;
  }
   >button {
     margin-left: 20px;
   }
 .img-wrap {
   padding: 40px;
  img {
    @include section-img; 
  }
 }
  .faq {
     border-top: 0.5px $gold solid;
     padding: 10px;
     margin: 0 20px;
   .question {
     @include flex;
     flex-direction: row;
     justify-content: space-around;
     button {
       margin-top: 0;
     } 
     h5 {
       text-align: center;
       @include headings;
       font-size: 17px;
       margin-bottom: 0;
     }
    .icon-plus {
      color: white;
    }
   .icon-minus {
     color: red;
   }
   } 
  .answer {
      p {
        color: white;
        margin: 20px;
        font-size: 15px;
        letter-spacing: 2px;
      }
    } 
  } 
} 

#reviews {
  h1 { 
    @include headings; 
    margin-bottom: 70px;
  }
    margin: 100px 0;
  .reviews {
    overflow: hidden;
    .review {
      margin-bottom: 50px;
      @include flex;
      position: relative;
     .img-bg {
      box-shadow: $shadow;
       border: 2px $gold solid;
     //  border-bottom: none;
      border-radius: 2000px;
        margin-bottom: -30px;
        position: relative;
        z-index: 100; 
        padding: 20px;
        background: $body;  
        img {
        max-width: 200px;
        border: none; 
        border-radius: 100%;
        transition: $transition;
        &:hover{
        transform: rotate(360deg);
      }
    } 
     }
     
      h1 {
        @include headings;
        margin-bottom: 0;
        margin-top: -25px;
        background: $gold;
        color: $body;
        padding: 0 10px;
        box-shadow: $shadow;
        &:hover {
          background: white;
          color: $gold;
        }  
      }
      p {
        border: 3px $gold solid;
        padding: 40px 20px;
        border-radius: 50px 0 20px 0;
        color: white;
        text-align: center;
        font-size: 15px;
        font-style: italic;
        margin: 0 20px;
        transition: $transition;
        box-shadow: $shadow;
        &:hover {
          background: $gold;
          border: 3px white solid;
          color: $body;
          box-shadow: $shadow;
        }
      }
    }
  } 

} 



#blog-posts {
  margin: 100px 0;
  h1 {
    @include headings;
  }
  button {
    margin-top: -65px;
    margin-left: 20px;
  }
} 

.blog-posts {
  margin: 10px;
 .blog-post {
   margin: 50px 0;
   img {
     Max-width: 100%;
     margin-bottom: 10px;
   }
  h2 {
    color: $gold;
    font-size: 20px;
    font-style: italic;
    text-transform: capitalize;
  }
 .blog-details {
   display: flex;
   flex-direction: row;
   margin: 10px 0;
   .icon {
     color: $gold;
   }
   p {
     margin-left: 10px;
     margin-right: 18px;
     color: white;
   }
 }
  .content {
   color: white;
   font-size: 15px;
   margin-bottom: 15px;
 }
 
 a {
   text-decoration: none;
   font-size: 15px;
   color: $gold;
 }
 }
}














@media (min-width: 688px){
 .reviews {
   display: grid;
   grid-template-columns: 1fr 1fr 1fr;
 }
}