@import 'App.scss';

@mixin grid {
   display: grid;
   grid-template-columns: 1fr 1fr 1fr;
   align-items: center; 
}

@media (min-width: 688px){
  .qualities {
    flex-direction: row;
   //margin-top: -100px;
   .quality {
      border-bottom: none;
      border-right: 0.05px $gold solid; 
      padding-top: 15px;
      padding-bottom: 10px;
      h1 {
        margin-bottom: 4px;
      }
    } 
  }
  
  
 #team {
   @include grid;
   grid-template-columns: 1fr 1fr;
   grid-gap: 50px;
 }
  
.specialties {
  @include grid;
  grid-gap: 50px;
}
  
#reasons {
  @include grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
}
  
#attorney {
 @include grid;
  align-items: center;
}
  
.FAQ {
 .container {
   @include grid;
   grid-template-columns: 1fr 1fr;
 }
}

.blog-posts {
  display: flex;
}

#newsletter {
  position: relative;
  z-index: 100;
  margin: 0 60px;
  margin-bottom: -100px;
}

#footer {
 padding-top: 130px;
.social {
  @include flex;
  flex-direction: row;
  margin-top: 20px;
}
 #pages {
   display: grid;
   grid-template-columns: 2fr 1fr 1fr;
   grid-gap: 50px;
   justify-items: center;
 }
.contact {
  h1 {
    text-align: center;
    margin: 50px 0 20px;
  }
  .container {
    @include flex;
    flex-direction: row;
   .icon {
     margin-left: 30px;
   }
  }
 }
.legal {
  margin: 50px;
  a {
    font-size: 18px;
  }
}

}
} 