@import 'App.scss';

@mixin link {
  color: white;
  font-size: 15px;
  transition: $transition;
  text-decoration: none;
  &:hover {
    color: $gold;
  }
}

#newsletter {
  padding: 60px 40px;
  background: $gold;
  h1{
    @include headings;
    color: $body;
  }
 .newsletter {
   @include flex;
   position: relative;
   input {
     width: 100%;
     padding: 12px;
     border: 2px black solid;
   }
   ::placeholder {
     font-size: 15px;
   }
   button {
     position: absolute;
     top: -16.5px;
     right: 5.5px;
   }
 }
}


#footer {
  background: $blue-hue;
  padding: 20px;
  padding-bottom: 100px;
  p {
    font-size: 15px;
    color: rgb(213,213,213);
  }
  
  h1 {
    @include headings;
    text-align: left;
    margin-top: 20px;
  }
  a {
    @include link;
    color: rgb(213,213,213);
  }
  li {
    list-style: none;
  }
  
 .address, .phone, .email {
   display: flex;
   flex-direction: row;
 }
 
.icon {
   color: $gold;
   margin-right: 15px;
   margin-bottom: 10px;
 }
 
.social-icon {
  color: $gold;
}

.legal {
  @include flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 20px;
  background: $body;
  padding: 15px;
  box-shadow: $shadow;
  a {
    margin-right: 5px;
    border-right: 2px rgb(213,213,213) solid;
    padding-right: 5px;
  }
  .help {
     border-right: none;
     margin-top: 4px;
   }  
  }
.credit {
  @include flex;
  h5 {
    color: white;
  }
  a {
    color: $gold;
  }
}
}

.contact-form {
  margin: 50px 20px;
  h1 {
    @include headings;
  }
  form {
    background: $blue-hue;
    display: flex;
    flex-direction: column;
    padding: 20px;
    label {
      color: $gold;
      font-size: 15px;
      margin: 25px 0 10px 0px;
    }
    input {
      padding: 10px;
    }
    textarea {
      height: 200px;
      padding: 10px;
    }
  }
}

