$gold:  #ffa300;

@mixin flex {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}


@mixin headings {
  font-size: 25px;
  color: $gold;
  text-align: center;
  margin-bottom: 20px;
}

#slider {
  width: 100%;
//  height: 100vh;
 .carousel {
   position: relative;
   max-width: 100%;
   margin: 0 auto;
   overflow: hidden;
   &::after {
       content: '';
       background: rgb(0,0,0, 0.5);
       position: absolute;
       top: 0;
       left: 0;
       height: 100%;
       width: 100%;
     } 
   img {
     position: relative;
     max-width: 150%;
   }
 }
}

.slider-content {
  @include flex;
  padding: 0 20px;
  position: absolute;
  top: 30%;
  z-index: 100;
  p {
    color: white;
    text-align: center;
    margin-bottom: 20px;
  }
  
  h1 {
    @include headings;
  }
}

@media (min-width: 688px) {
  
  #slider {
    .carousel {
      position: relative;
      img {
        min-width: 300%;
        min-height: 100%;
        transform: translateX(-30%);
      }
    }
   .slider-content {
     h1 {
       font-size: 35px;
     }
     p {
       font-size: 30px;
     }
     button {
       a {
         font-size: 25px;
       }
     }
   }
    
    
    
  } 
  
}